<template>
<div class="user-circle">
  <div class="avatar" @click="menu = true" :style="objStyles">
    <input type="file" @change="onFileChange" style="display: hidden" ref="input">
  </div>

  <div class="name">{{ user.name || 'Lover 1' }}</div>

  <v-bottom-sheet v-model="menu">
      <v-list dense>
        <v-list-item link @click="selectImage">
          <v-list-item-icon><v-icon>mdi-account-circle-outline</v-icon></v-list-item-icon>
          <v-list-item-content>Change avatar</v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-icon><v-icon>mdi-card-text-outline</v-icon></v-list-item-icon>
          <v-list-item-content>Change name</v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-icon><v-icon>mdi-eyedropper-variant</v-icon></v-list-item-icon>
          <v-list-item-content>Change border color</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-bottom-sheet>
</div>
</template>

<script>
export default {
  props: {
    user: {
      type: Object,
      required: true,
    },
    name: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      menu: false,
      avatar: 'https://c7.uihere.com/files/981/1001/235/q-version-cartoon-avatar-comics-significant-other-hand-painted-cartoon-cute-boy.jpg',
    }
  },
  computed: {
    objStyles() {
      let styles = {}
      styles.backgroundImage = 'url(' + this.avatar + ')'
      return styles
    }
  },
  mounted() {
    if(this.user.avatar.size) {
      this.avatar = URL.createObjectURL(this.user.avatar)
    }
  },
  methods: {
    selectImage() {
      this.$refs.input.click()
    },
    onFileChange() {
      if (this.$refs.input.files.length) {
        this.$store.commit('SET_AVATAR', {
          name: this.name,
          file: this.$refs.input.files[0]
        })
      }
    }
  },
  watch: {
    'user.avatar'(blob) {
      if(blob.size) {
        this.avatar = URL.createObjectURL(blob)
      }
    }
  }
}
</script>

<style lang="scss">
.user-circle {
  display: inline-block;
  margin: 10px;

  .avatar {
    width: 120px;
    height: 120px;
    border: 2px solid #ff30a2;
    border-radius: 50%;
    background-size: cover;
  }

  input[type="file"] {
    display: none;
  }

  .name {
    font-family: 'Pacifico', cursive;
  }
}
</style>